/* --------hiding scrollbar of app ------------ */
body::-webkit-scrollbar {
  display: none;
}

/* --------------Some Custom Css ---------------- */
.App {
  text-align: center;
  color: #fff;
}

/* ------------class taskBg Css ------------ */
.taskBg {
  background-color: #555;
  border-radius: 6px;
  margin-bottom: 10px;
  text-align: left;
  font-size: 22px;
  position: relative;
  padding: 10px 140px 10px 15px;
}

.taskBg div {
  display: flex;
  align-items: center;
}

/* -------------task number---------------- */
.taskNumber {
  color: #999;
  border: 1px solid #fff;
  border-radius: 50%;
  width: 26px;
  height: 26px;
  display: flex;
  text-align: center;
  font-size: 14px;
  margin-right: 8px;
  align-items: center;
  justify-content: center;
}

.taskBg .taskText {
  flex: 1 1;
}
.taskBg .done .taskText {
  text-decoration: line-through;
  opacity: 0.4;
}

/* -----------edit delete marked icons----------- */
.taskBg .iconsWrap {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  right: 15px;
  width: auto;
  display: inline-block;
}

.taskBg .iconsWrap span {
  display: inline-block;
  margin: 0px 10px 0 10px;
  cursor: pointer;
  color: #00ff89;
}

.taskBg .iconsWrap span:hover {
  color: yellow;
}
/* ---------my Custom Length------------- */
.mr-20 {
  margin-right: 10px;
}

/* ----------todo Heading-------------- */
#todo-heading {
  color: #fff;
  text-align: center;
  font-size: 20px;
  margin: 10px 0px;
}

/* -------------resposiveness of Small Device ----------------- */
@media screen and (max-width: 500px) {
  .taskBg .iconsWrap span {
    margin: 0px 4px;
  }
  .taskBg {
    font-size: 16px;
  }
}

